import { single } from '@mondobrain/utils';

import { Registry } from './utils';

class MondoCharts {
  constructor() {
    this.registry = new Registry();

    this.redrawAll = this.redrawAll.bind(this);
    this.renderAll = this.renderAll.bind(this);
  }

  redrawAll(last = true) {
    const instances = this.registry.list();

    instances.forEach(i => {
      i.redraw(last);
    });
  }

  renderAll() {
    const instances = this.registry.list();

    instances.forEach(i => {
      i.render();
    });
  }
}

export default single(module, 'MC', () => new MondoCharts());
